import React, { useState } from "react";
import {Navigate, useParams} from "react-router-dom";
import {remove_conv} from "../actions/queries";


const Discussion = (props) => {

    const redirectToConv = () => {
        window.location.replace(`/login/conv/${props.conv_id}`)
    };

    const deleteConv = (event) => {
        event.stopPropagation();
        remove_conv(props.conv_id);

    }

    return (
        <div className={`card ${ props.selected ? "selected-conv" : ""}`} >
            <div className="card-header">
                {props.receiverName}
                <p >{props.timeStamp}</p>
                <button style={{zIndex: 10, position: "relative"}} type="button" onClick={deleteConv} className="close">
                    <span aria-hidden="true">&times;</span>
                </button>
            </div>
            <div className="card-body">
                <p className="card-text">{props.lastMessageText}</p>
            </div>
        </div>
    );
};



export default Discussion;