import React, { useState, useEffect } from "react";
import {connect} from "react-redux";
import {Navigate} from "react-router-dom";
import Discussion from "../components/Discussion";
import Chat from "../components/Chat"
import {formatDate} from "../utils/utils";
import {create_new_conv, fetch_conv, get_auto_complete} from "../actions/queries";
import {logout} from "../actions/auth";

function Chat2({ isAuthenticated}) {
    // if (!isAuthenticated) {
    //     return <Navigate replace to="/login" />
    // }

    const [conversations, setConversations] = useState([]);
    const [query, setQuery] = useState('');
    const [suggestions, setSuggestions] = useState([]);

    // formatDate("2023-05-20T15:30:00")

    const fetchAutocompleteSuggestions = async () => {
        const sugge = await get_auto_complete(query);
        setSuggestions(sugge);
    };

    const handleInputChange = (event) => {
        const value = event.target.value;
        setQuery(value);
    };

    useEffect(() => {
        if (query) {
            fetchAutocompleteSuggestions();
        } else {
            setSuggestions([]);
        }
    }, [query]);

    const handleSuggestionClick = (suggestion) => {
        setQuery(suggestion);
        setSuggestions([]);
    };

    // load les discussion associ  un utilisateur

    // avoir un bouton pour cre une nouvelle discussions

    // un composant chat qui contien la discussion slctionn

    // il faut un useState selected discussion

    // un component discussion

    const newConvEvent = async (e) => {
        e.preventDefault();
        create_new_conv(query);
        // const temp_conv = [...conversations];
        // const res = await fetch_conv(temp_conv, setConversations);
        // setConversations(res);

    };

    const refresh = async () => {
        const temp_conv = [...conversations];
        const res = await fetch_conv(temp_conv, setConversations);
        if (res)
            setConversations(res);
        else
            setConversations([]);
    }

    useEffect(() => {

        refresh();
        const interval = setInterval(async () => {

            refresh();
        }, 1000);
        return () => clearInterval(interval);
    }, []);

    return (
        <div>
            <div className="row">
                <div className="col-3" >
                    {conversations.map((d) =>(
                        <Discussion
                            key={d.conv_id}
                            conv_id={d.conv_id}
                            lastMessageText={d.lastMessage}
                            receiverName={d.receivers}
                            timeStamp={d.timestamp}
                            selected
                        />
                    ))}


                </div>
                <div className="col-9" >
                    {/* <Chat/>*/}
                    <p>test</p>
                </div>
            </div>
            <div className="row">
                <div className="col-3" >
                    <button type="button" className="btn btn-primary" data-bs-toggle="modal"
                            data-bs-target="#exampleModal">
                        New Conversation
                    </button>
                </div>




                <div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                    <div class="modal-dialog" role="document">
                        <div class="modal-content">
                            <div class="modal-header">
                                <h5 class="modal-title" id="exampleModalLabel">Choose recipient</h5>
                                <button type="button" class="close" data-bs-dismiss="modal" aria-label="Close">
                                    <span aria-hidden="true">&times;</span>
                                </button>
                            </div>
                            <div class="modal-body">
                                <form>
                                    <div className="mb-3">
                                        <label htmlFor="exampleInputPassword1" className="form-label">Recipient</label>
                                        <input type="text" onChange={handleInputChange} value={query} className="form-control" id="exampleInputPassword1" />
                                        <ul>
                                            {suggestions.map((suggestion) => (
                                                <li key={suggestion} onClick={() => handleSuggestionClick(suggestion)}>
                                                    {suggestion}
                                                </li>
                                            ))}
                                        </ul>
                                    </div>
                                    <button type="submit" onSubmit={refresh} onClick={newConvEvent} className="btn btn-primary">Submit</button>
                                </form>
                            </div>

                        </div>
                    </div>
                </div>
                <div className="col-9" >
                    {/* <Chat/>*/}
                    <p>test</p>
                </div>
            </div>
        </div>

    );
}


const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated
});
export default connect(mapStateToProps, {})(Chat2);